import { supabase, testSupabaseConnection } from './supabaseClient';
import { sendOTPSMS } from './smsService';
import { BACKEND_URL } from '../config/backend';

const DEFAULT_PAGE_SIZE = 50;

// ===== TESTS ET VRIFICATIONS =====

export const testAllConnections = async () => {
  try {
    console.log('[TEST] Vrification de la connexion Supabase...');
    
    // Vrifier si le client Supabase est initialis
    if (!supabase) {
      console.warn('[TEST]  Client Supabase non initialis - configuration manquante');
      return { success: false, error: 'Configuration Supabase manquante' };
    }
    
    // Test de connexion de base
    const connectionTest = await testSupabaseConnection();
    if (!connectionTest.success) {
      throw new Error(`Connexion Supabase choue: ${connectionTest.error}`);
    }
    
    console.log('[TEST]  Connexion Supabase OK');
    
    // Test des tables principales
    const tables = ['users', 'otp_codes', 'loans', 'payments'];
    for (const table of tables) {
      try {
        const { error } = await supabase
          .from(table)
          .select('count')
          .limit(1);
        
        if (error) {
          console.warn(`[TEST]  Table ${table} non accessible:`, error.message);
        } else {
          console.log(`[TEST]  Table ${table} accessible`);
        }
      } catch (error) {
        console.warn(`[TEST]  Table ${table} non accessible:`, error.message);
      }
    }
    
    return { success: true, message: 'Tous les tests de base sont passs' };
  } catch (error) {
    console.error('[TEST]  Erreur lors des tests:', error.message);
    return { success: false, error: error.message };
  }
};

// ===== TRADUCTION DES ERREURS =====

const translateSupabaseError = (error) => {
  const errorMessage = error.message || error;
  const errorDetails = error.details || '';
  const errorHint = error.hint || '';
  
  // Combiner tous les dtails d'erreur pour une meilleure dtection
  const fullErrorText = `${errorMessage} ${errorDetails} ${errorHint}`.toLowerCase();
  
  // Erreurs de tlphone dj utilis
  if (fullErrorText.includes('phone_number') && (fullErrorText.includes('duplicate') || fullErrorText.includes('unique'))) {
    return 'Ce numro de tlphone est dj utilis pour crer un compte. Veuillez utiliser un autre numro de tlphone.';
  }
  
  // Erreurs d'email dj utilis
  if (fullErrorText.includes('email') && (fullErrorText.includes('duplicate') || fullErrorText.includes('unique'))) {
    return 'Cette adresse email est dj utilise. Veuillez utiliser une autre adresse email.';
  }
  
  // Erreurs de mot de passe
  if (fullErrorText.includes('password')) {
    if (fullErrorText.includes('weak')) {
      return 'Le mot de passe est trop faible. Il doit contenir au moins 6 caractres.';
    }
    return 'Erreur avec le mot de passe. Veuillez vrifier votre saisie.';
  }
  
  // Erreurs de validation
  if (fullErrorText.includes('invalid')) {
    if (fullErrorText.includes('email')) {
      return 'Adresse email invalide. Veuillez vrifier votre saisie.';
    }
    if (fullErrorText.includes('phone')) {
      return 'Numro de tlphone invalide. Veuillez vrifier votre saisie.';
    }
    return 'Donnes invalides. Veuillez vrifier vos informations.';
  }
  
  // Erreurs de rseau
  if (fullErrorText.includes('network') || fullErrorText.includes('fetch')) {
    return 'Erreur de connexion. Veuillez vrifier votre connexion internet et ressayer.';
  }
  
  // Erreurs de serveur
  if (fullErrorText.includes('server') || fullErrorText.includes('500')) {
    return 'Erreur du serveur. Veuillez ressayer dans quelques minutes.';
  }
  
  // Erreurs de quota/limite
  if (fullErrorText.includes('quota') || fullErrorText.includes('limit')) {
    return 'Limite de tentatives atteinte. Veuillez ressayer plus tard.';
  }
  
  // Erreurs gnriques - plus spcifiques
  if (fullErrorText.includes('already registered')) {
    // Essayer de dtecter quelle information spcifique est en conflit
    if (fullErrorText.includes('phone')) {
      return 'Un compte existe dj avec ce numro de tlphone.';
    }
    if (fullErrorText.includes('email')) {
      return 'Un compte existe dj avec cette adresse email.';
    }
    return 'Un compte existe dj avec ces informations.';
  }
  
  if (fullErrorText.includes('user already registered')) {
    if (fullErrorText.includes('phone')) {
      return 'Un utilisateur existe dj avec ce numro de tlphone.';
    }
    if (fullErrorText.includes('email')) {
      return 'Un utilisateur existe dj avec cette adresse email.';
    }
    return 'Un utilisateur avec ces informations existe dj.';
  }
  
  // Par dfaut, retourner le message d'erreur original
  return errorMessage;
};

// ===== AUTHENTIFICATION =====

export const signUpWithPhone = async (phoneNumber, password, userData) => {
  if (!supabase) {
    console.error('[SUPABASE] Client non initialis - configuration manquante');
    return { success: false, error: 'Configuration Supabase manquante' };
  }

  try {
    // Nettoyer et valider le numro de tlphone
    const cleanPhone = phoneNumber.replace(/[^0-9]/g, '');
    
    // Vrifier que le numro a au moins 8 chiffres
    if (cleanPhone.length < 8) {
      throw new Error('Numro de tlphone invalide');
    }
    
    // Utiliser l'email fourni ou crer un email temporaire
    let emailToUse;
    console.log('[SUPABASE] userData reu:', userData);
    console.log('[SUPABASE] Email fourni:', userData.email);
    
    if (userData.email && userData.email.trim()) {
      emailToUse = userData.email.trim();
      console.log(`[SUPABASE]  Utilisation email fourni: ${emailToUse}`);
    } else {
      // Crer un email temporaire unique et valide
      const timestamp = Date.now();
      emailToUse = `user.${cleanPhone}.${timestamp}@gmail.com`;
      console.log(`[SUPABASE]  Cration email temporaire: ${emailToUse}`);
    }
    
    console.log(`[SUPABASE] Cration utilisateur avec email: ${emailToUse}`);
    const { data: authData, error: authError } = await supabase.auth.signUp({
      email: emailToUse,
      password,
      options: {
        data: {
          first_name: userData.firstName,
          last_name: userData.lastName,
          phone_number: phoneNumber,
          role: 'client'
        }
      }
    });

    if (authError) {
      console.error('[SUPABASE] Erreur Auth:', authError);
      throw new Error(translateSupabaseError(authError));
    }

    // Insrer les donnes utilisateur dans notre table users
    const { error: userError } = await supabase
      .from('users')
      .insert([{
        id: authData.user.id,
        phone_number: phoneNumber,
        email: emailToUse, // Sauvegarder l'email utilis
        first_name: userData.firstName,
        last_name: userData.lastName,
        role: 'client',
        status: 'approved'
      }]);

    if (userError) {
      console.error('[SUPABASE] Erreur insertion utilisateur:', userError);
      throw new Error(translateSupabaseError(userError));
    }

    // Log de bienvenue (SMS temporairement dsactiv)
    console.log(`[SUPABASE]  Utilisateur cr: ${userData.firstName} ${userData.lastName} (${phoneNumber})`);

    return { success: true, user: authData.user };
  } catch (error) {
    console.error('[SUPABASE] Erreur lors de l\'inscription:', error.message);
    const translatedError = translateSupabaseError(error);
    return { success: false, error: translatedError };
  }
};

