export const updateLoanStatus = async (loanId, status, adminId = null) => {
  try {
    console.log('[UPDATE_LOAN_STATUS]  Mise  jour du prt:', { loanId, status, adminId });
    
    // Validation du statut
    const validStatuses = ['pending', 'approved', 'active', 'completed', 'rejected'];
    if (!validStatuses.includes(status)) {
      throw new Error(`Statut invalide: ${status}. Statuts autoriss: ${validStatuses.join(', ')}`);
    }

    const updateData = { 
      status,
      updated_at: new Date().toISOString()
    };
    
    if (status === 'approved') {
      // Quand un prt est approuv, il devient automatiquement actif
      updateData.status = 'active';
      updateData.approved_by = adminId;
      updateData.approved_at = new Date().toISOString();
    }

    console.log('[UPDATE_LOAN_STATUS]  Donnes  mettre  jour:', updateData);

    const { data, error } = await supabase
      .from('loans')
      .update(updateData)
      .eq('id', loanId)
      .select(`
        *,
        users!inner(id, first_name, last_name)
      `)
      .single();

    if (error) {
      console.error('[UPDATE_LOAN_STATUS]  Erreur Supabase:', {
        message: error.message,
        details: error.details,
        hint: error.hint,
        code: error.code
      });
      
      // Vrifier si c'est une erreur de contrainte
      if (error.code === '23514' || error.message?.includes('check constraint')) {
        throw new Error(`La base de donnes n'autorise pas le statut "${status}". Veuillez contacter l'administrateur pour mettre  jour la contrainte.`);
      }
      
      throw error;
    }

    console.log('[UPDATE_LOAN_STATUS]  Prt mis  jour avec succs:', data);

    // Systme hybride de notifications (in-app + push)
    if (data && (status === 'approved' || status === 'rejected')) {
      try {
        const isApproved = status === 'approved';
        const action = isApproved ? 'approbation' : 'refus';
        console.log(`[LOAN_${action.toUpperCase()}] Systme hybride de notifications...`);
        
        // 1. TOUJOURS crer la notification dans la base de donnes (in-app)
        const { error: notificationError } = await supabase
          .from('notifications')
          .insert({
            user_id: data.user_id,
            title: isApproved ? 'Prt approuv ! ' : 'Demande de prt refuse',
            message: isApproved 
              ? `Votre demande de prt de ${parseInt(data.amount).toLocaleString()} FCFA a t approuve. Vous pouvez maintenant effectuer votre premier remboursement.`
              : `Votre demande de prt de ${parseInt(data.amount).toLocaleString()} FCFA a t refuse. Contactez l'administration pour plus d'informations.`,
            type: 'loan_status',
            data: {
              loan_id: data.id,
              loan_amount: data.amount,
              status: isApproved ? 'approved' : 'rejected',
              action: isApproved ? 'approved' : 'rejected'
            },
            read: false
          });

        if (notificationError) {
          console.error(`[LOAN_${action.toUpperCase()}]  Erreur cration notification DB:`, notificationError);
        } else {
          console.log(`[LOAN_${action.toUpperCase()}]  Notification in-app cre (TOUJOURS disponible)`);
        }

        // 2. Tenter d'envoyer la notification push (si abonnement disponible)
        try {
          const notificationResponse = await fetch(`${BACKEND_URL}/api/notify-loan-${action}`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              userId: data.user_id,
              loanAmount: data.amount,
              loanId: data.id,
              status: isApproved ? 'approved' : 'rejected'
            })
          });

          if (notificationResponse.ok) {
            console.log(`[LOAN_${action.toUpperCase()}]  Notification push envoye (utilisateur hors ligne notifi)`);
          } else {
            console.log(`[LOAN_${action.toUpperCase()}]  Push chou mais notification in-app disponible`);
          }
        } catch (pushError) {
          console.log(`[LOAN_${action.toUpperCase()}]  Push non disponible mais notification in-app cre`);
        }

        console.log(`[LOAN_${action.toUpperCase()}]  Systme hybride : Notification garantie (in-app + push si disponible)`);
      } catch (notificationError) {
        console.error(`[LOAN_NOTIFICATION]  Erreur systme de notifications:`, notificationError);
        // Mme en cas d'erreur, la notification in-app est prioritaire
      }
    }

    return { success: true, data };
  } catch (error) {
    console.error('[UPDATE_LOAN_STATUS]  Erreur lors de la mise  jour du prt:', error);
    const errorMessage = error.message || 'Erreur lors de la mise  jour du statut du prt';
    return { success: false, error: errorMessage };
  }
};

