// Utilitaire pour forcer le rafrachissement du rle utilisateur
import { supabase } from './supabaseClient';

export const forceRoleRefresh = async (userId) => {
  try {
    console.log('[FORCE_ROLE_REFRESH]  Dbut du rafrachissement forc du rle...');
    
    // 1. Nettoyer compltement le cache
    console.log('[FORCE_ROLE_REFRESH]  Nettoyage du cache...');
    localStorage.removeItem('ab_user_cache');
    localStorage.removeItem('ab_user_cache_time');
    sessionStorage.clear();
    
    // 2. Rcuprer la session actuelle
    const { data: { session }, error: sessionError } = await supabase.auth.getSession();
    
    if (sessionError) {
      console.error('[FORCE_ROLE_REFRESH]  Erreur session:', sessionError);
      return { success: false, error: sessionError.message };
    }
    
    if (!session?.user) {
      console.log('[FORCE_ROLE_REFRESH]  Aucune session active');
      return { success: false, error: 'Aucune session active' };
    }
    
    console.log('[FORCE_ROLE_REFRESH]  Session trouve:', session.user.email);
    
    // 3. Rcuprer le rle depuis la DB
    console.log('[FORCE_ROLE_REFRESH]  Rcupration du rle depuis la DB...');
    const { data: dbUser, error: dbError } = await supabase
      .from('users')
      .select('role, first_name, last_name, email')
      .eq('id', session.user.id)
      .single();
    
    if (dbError) {
      console.error('[FORCE_ROLE_REFRESH]  Erreur DB:', dbError);
      return { success: false, error: dbError.message };
    }
    
    if (!dbUser) {
      console.error('[FORCE_ROLE_REFRESH]  Utilisateur non trouv en DB');
      return { success: false, error: 'Utilisateur non trouv' };
    }
    
    console.log('[FORCE_ROLE_REFRESH]  Rle rcupr:', dbUser.role);
    
    // 4. Mettre  jour le cache avec les bonnes donnes
    const userData = {
      id: session.user.id,
      email: session.user.email,
      role: dbUser.role,
      first_name: dbUser.first_name || '',
      last_name: dbUser.last_name || '',
      firstName: dbUser.first_name,
      lastName: dbUser.last_name
    };
    
    localStorage.setItem('ab_user_cache', JSON.stringify(userData));
    localStorage.setItem('ab_user_cache_time', Date.now().toString());
    
    console.log('[FORCE_ROLE_REFRESH]  Cache mis  jour avec le rle:', dbUser.role);
    
    // 5. Forcer un rechargement de la page pour appliquer les changements
    console.log('[FORCE_ROLE_REFRESH]  Rechargement de la page...');
    window.location.reload();
    
    return { 
      success: true, 
      role: dbUser.role,
      user: userData 
    };
    
  } catch (error) {
    console.error('[FORCE_ROLE_REFRESH]  Erreur gnrale:', error);
    return { success: false, error: error.message };
  }
};

// Fonction pour vrifier si le rle est correct
export const checkRoleConsistency = async () => {
  try {
    const cached = localStorage.getItem('ab_user_cache');
    if (!cached) {
      return { consistent: false, reason: 'Aucun cache trouv' };
    }
    
    const cachedUser = JSON.parse(cached);
    const { data: { session } } = await supabase.auth.getSession();
    
    if (!session?.user) {
      return { consistent: false, reason: 'Aucune session active' };
    }
    
    // Rcuprer le rle depuis la DB
    const { data: dbUser, error: dbError } = await supabase
      .from('users')
      .select('role')
      .eq('id', session.user.id)
      .single();
    
    if (dbError || !dbUser) {
      return { consistent: false, reason: 'Impossible de rcuprer le rle depuis la DB' };
    }
    
    const isConsistent = cachedUser.role === dbUser.role;
    
    return {
      consistent: isConsistent,
      cachedRole: cachedUser.role,
      dbRole: dbUser.role,
      reason: isConsistent ? 'Rles cohrents' : 'Rles incohrents'
    };
    
  } catch (error) {
    console.error('[CHECK_ROLE_CONSISTENCY]  Erreur:', error);
    return { consistent: false, reason: error.message };
  }
};
