// Script de test pour vrifier la configuration PWA
export const testPWAConfiguration = () => {
  console.log(' Test de la configuration PWA...');
  
  // Vrifier si le service worker est support
  if ('serviceWorker' in navigator) {
    console.log(' Service Worker support');
  } else {
    console.log(' Service Worker non support');
  }
  
  // Vrifier si l'API d'installation est disponible
  if ('beforeinstallprompt' in window) {
    console.log(' API d\'installation PWA disponible');
  } else {
    console.log(' API d\'installation PWA non disponible');
  }
  
  // Vrifier si l'app est en mode standalone
  if (window.matchMedia('(display-mode: standalone)').matches) {
    console.log(' App en mode standalone (installe)');
  } else {
    console.log(' App en mode navigateur');
  }
  
  // Vrifier le manifest
  const manifestLink = document.querySelector('link[rel="manifest"]');
  if (manifestLink) {
    console.log(' Manifest.json trouv:', manifestLink.href);
  } else {
    console.log(' Manifest.json non trouv');
  }
  
  // Vrifier les meta tags PWA
  const themeColor = document.querySelector('meta[name="theme-color"]');
  if (themeColor) {
    console.log(' Theme color dfini:', themeColor.content);
  } else {
    console.log(' Theme color non dfini');
  }
  
  const appleTouchIcon = document.querySelector('link[rel="apple-touch-icon"]');
  if (appleTouchIcon) {
    console.log(' Apple touch icon dfini:', appleTouchIcon.href);
  } else {
    console.log(' Apple touch icon non dfini');
  }
  
  // Vrifier le cache
  if ('caches' in window) {
    console.log(' Cache API disponible');
    caches.keys().then(cacheNames => {
      console.log(' Caches disponibles:', cacheNames);
    });
  } else {
    console.log(' Cache API non disponible');
  }
  
  console.log(' Test PWA termin');
};

// Fonction pour forcer l'affichage du prompt d'installation (pour les tests)
export const forceInstallPrompt = () => {
  console.log(' Forage de l\'affichage du prompt d\'installation...');
  
  // Simuler l'vnement beforeinstallprompt
  const event = new Event('beforeinstallprompt');
  event.preventDefault = () => {};
  event.prompt = () => Promise.resolve({ outcome: 'accepted' });
  event.userChoice = Promise.resolve({ outcome: 'accepted' });
  
  window.dispatchEvent(event);
  console.log(' vnement beforeinstallprompt simul');
};

// Fonction pour vrifier l'tat d'installation
export const checkInstallationStatus = () => {
  const isInstalled = window.matchMedia('(display-mode: standalone)').matches || 
                     window.navigator.standalone === true;
  
  const hasSeenPrompt = localStorage.getItem('pwa-install-prompt-seen');
  const hasAccepted = localStorage.getItem('pwa-install-accepted');
  const hasDeclined = localStorage.getItem('pwa-install-declined');
  
  console.log(' tat d\'installation:', {
    isInstalled,
    hasSeenPrompt: !!hasSeenPrompt,
    hasAccepted: !!hasAccepted,
    hasDeclined: !!hasDeclined
  });
  
  return {
    isInstalled,
    hasSeenPrompt: !!hasSeenPrompt,
    hasAccepted: !!hasAccepted,
    hasDeclined: !!hasDeclined
  };
};

// Fonction pour rinitialiser l'tat d'installation (pour les tests)
export const resetInstallationState = () => {
  console.log(' Rinitialisation de l\'tat d\'installation...');
  
  localStorage.removeItem('pwa-install-prompt-seen');
  localStorage.removeItem('pwa-install-prompt-last-seen');
  localStorage.removeItem('pwa-install-accepted');
  localStorage.removeItem('pwa-install-declined');
  localStorage.removeItem('pwa-install-declined-date');
  localStorage.removeItem('pwa-installed');
  localStorage.removeItem('pwa-install-date');
  
  console.log(' tat d\'installation rinitialis');
};

// Exporter toutes les fonctions pour utilisation dans la console
if (typeof window !== 'undefined') {
  window.testPWA = {
    testConfiguration: testPWAConfiguration,
    forceInstallPrompt,
    checkInstallationStatus,
    resetInstallationState
  };
  
  console.log(' Fonctions de test PWA disponibles dans window.testPWA');
}
