/**
 * Utilitaire pour grer le cache de l'application
 * Permet de forcer le rechargement en cas de mise  jour
 */

const APP_VERSION = '2.0.0';
const VERSION_KEY = 'ab_app_version';

/**
 * Vrifie si l'application a t mise  jour
 * et force un rechargement du cache si ncessaire
 */
export const checkAndClearCache = () => {
  try {
    const storedVersion = localStorage.getItem(VERSION_KEY);
    
    if (storedVersion !== APP_VERSION) {
      console.log('[CACHE] Nouvelle version dtecte:', APP_VERSION, '(ancienne:', storedVersion, ')');
      
      // Mettre  jour la version
      localStorage.setItem(VERSION_KEY, APP_VERSION);
      
      // Nettoyer le cache de l'application
      clearApplicationCache();
      
      // Afficher un message  l'utilisateur
      console.log('[CACHE] Cache nettoy pour la version', APP_VERSION);
      
      return true;
    }
    
    return false;
  } catch (error) {
    console.error('[CACHE] Erreur lors de la vrification de version:', error);
    return false;
  }
};

/**
 * Nettoie le cache de l'application
 */
export const clearApplicationCache = async () => {
  try {
    // 1. Nettoyer le localStorage (sauf les donnes utilisateur essentielles)
    const essentialKeys = ['ab_user_cache', 'supabase.auth.token'];
    const allKeys = Object.keys(localStorage);
    
    allKeys.forEach(key => {
      if (!essentialKeys.some(essential => key.includes(essential))) {
        localStorage.removeItem(key);
      }
    });
    
    console.log('[CACHE] localStorage nettoy (sauf donnes essentielles)');
    
    // 2. Nettoyer le sessionStorage
    sessionStorage.clear();
    console.log('[CACHE] sessionStorage nettoy');
    
    // 3. Nettoyer le cache du Service Worker si disponible
    if ('caches' in window) {
      const cacheNames = await caches.keys();
      await Promise.all(
        cacheNames.map(cacheName => {
          console.log('[CACHE] Suppression du cache:', cacheName);
          return caches.delete(cacheName);
        })
      );
      console.log('[CACHE] Cache Service Worker nettoy');
    }
    
    // 4. Dsinscrire les anciens Service Workers si ncessaire
    if ('serviceWorker' in navigator) {
      const registrations = await navigator.serviceWorker.getRegistrations();
      for (let registration of registrations) {
        await registration.unregister();
        console.log('[CACHE] Service Worker dsinscrit');
      }
    }
    
    return true;
  } catch (error) {
    console.error('[CACHE] Erreur lors du nettoyage du cache:', error);
    return false;
  }
};

/**
 * Force un rechargement complet de l'application
 * (sans cache)
 */
export const forceReload = () => {
  console.log('[CACHE] Rechargement forc de l\'application...');
  
  // Recharger en contournant le cache
  if (window.location.reload) {
    window.location.reload(true); // true = bypass cache
  } else {
    // Fallback : recharger la page
    window.location.reload();
  }
};

/**
 * Obtenir la version actuelle de l'application
 */
export const getAppVersion = () => APP_VERSION;

/**
 * Vrifier si le cache doit tre nettoy au dmarrage
 */
export const initCacheManagement = () => {
  const wasUpdated = checkAndClearCache();
  
  if (wasUpdated) {
    console.log('[CACHE]  Application mise  jour vers la version', APP_VERSION);
    
    // Optionnel : afficher un message  l'utilisateur
    // Vous pouvez utiliser un toast ou une notification
    
    return true;
  }
  
  console.log('[CACHE] Application  jour (version', APP_VERSION, ')');
  return false;
};

const clearCacheModule = {
  checkAndClearCache,
  clearApplicationCache,
  forceReload,
  getAppVersion,
  initCacheManagement
};

export default clearCacheModule;


