import { useState, useEffect } from 'react';

const usePWAInstall = () => {
  const [deferredPrompt, setDeferredPrompt] = useState(null);
  const [isInstallable, setIsInstallable] = useState(false);
  const [isInstalled, setIsInstalled] = useState(false);
  const [showInstallPrompt, setShowInstallPrompt] = useState(false);

  useEffect(() => {
    // Vrifier si l'app est dj installe
    const checkIfInstalled = () => {
      // Vrifier si l'app est en mode standalone (installe)
      if (window.matchMedia('(display-mode: standalone)').matches) {
        setIsInstalled(true);
        return true;
      }
      
      // Vrifier si l'app est ajoute  l'cran d'accueil sur iOS
      if (window.navigator.standalone === true) {
        setIsInstalled(true);
        return true;
      }
      
      return false;
    };

    // Vrifier si l'utilisateur a dj vu le prompt d'installation
    const hasSeenInstallPrompt = localStorage.getItem('pwa-install-prompt-seen');
    const hasDismissedPrompt = localStorage.getItem('pwa-install-prompt-dismissed');
    const hasAcceptedInstall = localStorage.getItem('pwa-install-accepted');
    
    // Si l'utilisateur a dj accept l'installation, ne pas re-afficher
    if (hasAcceptedInstall === 'true') {
      console.log('[PWA_INSTALL] App dj accepte par l\'utilisateur');
      setIsInstalled(true);
      return;
    }
    
    // Si l'utilisateur a dj vu le prompt ET l'a rejet, ne pas le re-afficher
    if (hasSeenInstallPrompt === 'true' && hasDismissedPrompt === 'true') {
      console.log('[PWA_INSTALL] Prompt dj rejet par l\'utilisateur, pas d\'affichage');
      return;
    }

    // Vrifier si l'app est dj installe
    if (checkIfInstalled()) {
      console.log('[PWA_INSTALL] App dj installe');
      return;
    }

    // couter l'vnement beforeinstallprompt
    const handleBeforeInstallPrompt = (e) => {
      console.log('[PWA_INSTALL] beforeinstallprompt dclench');
      
      // Empcher l'affichage automatique du prompt
      e.preventDefault();
      
      // Vrifier  nouveau les prfrences avant d'afficher
      const hasSeenInstallPrompt = localStorage.getItem('pwa-install-prompt-seen');
      const hasDismissedPrompt = localStorage.getItem('pwa-install-prompt-dismissed');
      const hasAcceptedInstall = localStorage.getItem('pwa-install-accepted');
      
      if (hasAcceptedInstall === 'true' || (hasSeenInstallPrompt === 'true' && hasDismissedPrompt === 'true')) {
        console.log('[PWA_INSTALL] Prompt dj gr par l\'utilisateur - pas d\'affichage');
        return;
      }
      
      // Sauvegarder l'vnement pour l'utiliser plus tard
      setDeferredPrompt(e);
      setIsInstallable(true);
      
      // Afficher notre prompt personnalis aprs un dlai
      setTimeout(() => {
        setShowInstallPrompt(true);
      }, 3000); // Attendre 3 secondes aprs le chargement de la page
    };

    // couter l'vnement appinstalled
    const handleAppInstalled = () => {
      console.log('[PWA_INSTALL] App installe avec succs');
      setIsInstalled(true);
      setIsInstallable(false);
      setShowInstallPrompt(false);
      setDeferredPrompt(null);
      
      // Marquer que l'utilisateur a install l'app
      localStorage.setItem('pwa-installed', 'true');
      localStorage.setItem('pwa-install-date', Date.now().toString());
    };

    // Ajouter les couteurs d'vnements
    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
    window.addEventListener('appinstalled', handleAppInstalled);

    // Nettoyage
    return () => {
      window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
      window.removeEventListener('appinstalled', handleAppInstalled);
    };
  }, []);

  // Fonction pour dclencher l'installation
  const installApp = async () => {
    if (!deferredPrompt) {
      console.log('[PWA_INSTALL] Pas de prompt d\'installation disponible');
      return false;
    }

    try {
      console.log('[PWA_INSTALL] Dclenchement de l\'installation...');
      
      // Afficher le prompt d'installation
      deferredPrompt.prompt();
      
      // Attendre la rponse de l'utilisateur
      const { outcome } = await deferredPrompt.userChoice;
      
      console.log('[PWA_INSTALL] Rsultat de l\'installation:', outcome);
      
      if (outcome === 'accepted') {
        console.log('[PWA_INSTALL]  Installation accepte');
        setIsInstalled(true);
        setShowInstallPrompt(false);
        
        // Marquer que l'utilisateur a accept l'installation
        localStorage.setItem('pwa-install-accepted', 'true');
        localStorage.setItem('pwa-install-date', Date.now().toString());
      } else {
        console.log('[PWA_INSTALL]  Installation refuse');
        
        // Marquer que l'utilisateur a refus l'installation
        localStorage.setItem('pwa-install-declined', 'true');
        localStorage.setItem('pwa-install-declined-date', Date.now().toString());
      }
      
      // Nettoyer le prompt
      setDeferredPrompt(null);
      setIsInstallable(false);
      
      return outcome === 'accepted';
    } catch (error) {
      console.error('[PWA_INSTALL] Erreur lors de l\'installation:', error);
      return false;
    }
  };

  // Fonction pour fermer le prompt
  const dismissInstallPrompt = () => {
    console.log('[PWA_INSTALL] Prompt d\'installation ferm');
    setShowInstallPrompt(false);
    
    // Marquer que l'utilisateur a vu le prompt et l'a rejet
    localStorage.setItem('pwa-install-prompt-seen', 'true');
    localStorage.setItem('pwa-install-prompt-dismissed', 'true');
    console.log('[PWA_INSTALL] Prompt d\'installation rejet par l\'utilisateur');
  };

  // Fonction pour vrifier si l'app peut tre installe
  const canInstall = () => {
    return isInstallable && !isInstalled && deferredPrompt !== null;
  };

  return {
    isInstallable,
    isInstalled,
    showInstallPrompt,
    canInstall: canInstall(),
    installApp,
    dismissInstallPrompt
  };
};

export default usePWAInstall;
