/**
 * Configuration React Query : cache en mmoire, pas de refetch inutile
 * - staleTime: 2 min  les donnes sont considres fraches, pas de refetch au focus
 * - gcTime (ex cacheTime): 5 min  garde les donnes en mmoire 5 min aprs dernier usage
 * - refetchOnWindowFocus: false  ne pas recharger  chaque retour sur l'onglet
 * - refetchOnReconnect: true  rafrachir aprs perte de connexion
 */
import { QueryClient } from '@tanstack/react-query';

const STALE_TIME_MS = 2 * 60 * 1000;   // 2 minutes
const GC_TIME_MS = 5 * 60 * 1000;      // 5 minutes (garder en mmoire)

export const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      staleTime: STALE_TIME_MS,
      gcTime: GC_TIME_MS,
      refetchOnWindowFocus: false,
      refetchOnReconnect: true,
      retry: 1,
    },
  },
});

export default queryClient;
