import { useState, useEffect, useCallback } from 'react';

const CHECK_INTERVAL = 5 * 60 * 1000; // Vrifier toutes les 5 minutes
const VERSION_FILE = '/version.json';

export const useAppUpdate = () => {
  const [updateAvailable, setUpdateAvailable] = useState(false);
  const [isChecking, setIsChecking] = useState(false);
  const [currentVersion, setCurrentVersion] = useState(null);
  const [newVersion, setNewVersion] = useState(null);
  const [serviceWorkerRegistration, setServiceWorkerRegistration] = useState(null);

  // Fonction pour configurer les couteurs
  const setupServiceWorkerListeners = useCallback((registration) => {
    // couter les mises  jour du service worker
    registration.addEventListener('updatefound', () => {
      const newWorker = registration.installing;
      if (newWorker) {
        newWorker.addEventListener('statechange', () => {
          if (newWorker.state === 'installed' && navigator.serviceWorker.controller) {
            console.log('[APP_UPDATE] Nouveau Service Worker disponible');
            setUpdateAvailable(true);
          }
        });
      }
    });
  }, []);

  // Fonction pour mettre  jour le Service Worker de manire scurise
  const safeUpdateServiceWorker = useCallback((registration) => {
    if (!registration) return;
    
    try {
      // Vrifier que le registration est toujours actif
      navigator.serviceWorker.getRegistration(registration.scope)
        .then((activeRegistration) => {
          if (activeRegistration) {
            activeRegistration.update().catch((error) => {
              // Erreur silencieuse si le Service Worker a t dsinstall
              if (!error.message?.includes('uninstalled')) {
                if (process.env.NODE_ENV === 'development') {
                  console.log('[APP_UPDATE] Service Worker mise  jour ignore:', error.message);
                }
              }
            });
          }
        })
        .catch(() => {
          // Registration n'existe plus, c'est OK
        });
    } catch (error) {
      // Erreur silencieuse
      if (process.env.NODE_ENV === 'development') {
        console.log('[APP_UPDATE] Mise  jour Service Worker ignore:', error.message);
      }
    }
  }, []);

  // Enregistrer le service worker
  useEffect(() => {
    // Ne pas enregistrer le Service Worker en dveloppement (sauf si explicitement activ)
    if (process.env.NODE_ENV === 'development' && process.env.REACT_APP_ENABLE_SW !== 'true') {
      console.log('[APP_UPDATE] Service Worker dsactiv en dveloppement');
      return;
    }

    if ('serviceWorker' in navigator) {
      // Vrifier d'abord si un Service Worker est dj enregistr
      navigator.serviceWorker.getRegistration()
        .then((existingRegistration) => {
          if (existingRegistration) {
            console.log('[APP_UPDATE] Service Worker dj enregistr:', existingRegistration.scope);
            setServiceWorkerRegistration(existingRegistration);
            setupServiceWorkerListeners(existingRegistration);
            return existingRegistration;
          } else {
            // Enregistrer un nouveau Service Worker
            return navigator.serviceWorker.register('/serviceWorker.js');
          }
        })
        .then((registration) => {
          if (registration) {
            // Toujours configurer les couteurs et vrifier les mises  jour
            console.log('[APP_UPDATE] Service Worker enregistr:', registration.scope);
            setServiceWorkerRegistration((prev) => {
              // Ne configurer les couteurs que si ce n'est pas dj fait
              if (!prev || prev !== registration) {
                setupServiceWorkerListeners(registration);
              }
              return registration;
            });
            
            // Vrifier les mises  jour de manire scurise
            safeUpdateServiceWorker(registration);
          }
        })
        .catch((error) => {
          // Erreur silencieuse en dveloppement
          if (process.env.NODE_ENV === 'development') {
            console.log('[APP_UPDATE] Service Worker non disponible en dveloppement:', error.message);
          } else {
            console.error('[APP_UPDATE] Erreur enregistrement Service Worker:', error);
          }
        });
    }
  }, [setupServiceWorkerListeners, safeUpdateServiceWorker]);

  // Vrifier la version de l'application
  const checkVersion = useCallback(async () => {
    try {
      setIsChecking(true);
      
      // Rcuprer la version actuelle stocke
      const storedVersion = localStorage.getItem('app-version');
      const storedBuildNumber = localStorage.getItem('app-build-number');

      // Rcuprer la nouvelle version depuis le serveur (avec cache busting)
      const response = await fetch(`${VERSION_FILE}?t=${Date.now()}`, {
        cache: 'no-cache',
        headers: {
          'Cache-Control': 'no-cache',
          'Pragma': 'no-cache'
        }
      });

      if (!response.ok) {
        console.log('[APP_UPDATE] Fichier version.json non trouv, utilisation de la dtection Service Worker uniquement');
        return;
      }

      const versionData = await response.json();
      const { version, buildNumber, buildDate } = versionData;

      console.log('[APP_UPDATE] Version serveur:', { version, buildNumber, buildDate });
      console.log('[APP_UPDATE] Version stocke:', { version: storedVersion, buildNumber: storedBuildNumber });

      // Comparer avec la version stocke
      if (storedVersion && storedBuildNumber) {
        if (buildNumber > parseInt(storedBuildNumber) || version !== storedVersion) {
          console.log('[APP_UPDATE]  Nouvelle version dtecte !');
          setNewVersion(versionData);
          setUpdateAvailable(true);
        }
      } else {
        // Premire visite, stocker la version actuelle
        localStorage.setItem('app-version', version);
        localStorage.setItem('app-build-number', buildNumber.toString());
        setCurrentVersion(versionData);
      }

      // Mettre  jour la version stocke
      if (version) {
        localStorage.setItem('app-version', version);
        localStorage.setItem('app-build-number', buildNumber.toString());
      }

    } catch (error) {
      console.error('[APP_UPDATE] Erreur lors de la vrification:', error);
    } finally {
      setIsChecking(false);
    }
  }, []);

  // Vrification initiale
  useEffect(() => {
    checkVersion();

    // Vrifier rgulirement
    const interval = setInterval(checkVersion, CHECK_INTERVAL);

    // Vrifier aussi quand la fentre reprend le focus
    const handleFocus = () => {
      checkVersion();
    };
    window.addEventListener('focus', handleFocus);

    // Vrifier le service worker rgulirement (de manire scurise)
    if (serviceWorkerRegistration) {
      const swInterval = setInterval(() => {
        // Vrifier que le Service Worker est toujours actif avant de mettre  jour
        navigator.serviceWorker.getRegistration(serviceWorkerRegistration.scope)
          .then((registration) => {
            if (registration) {
              registration.update().catch((error) => {
                // Erreur silencieuse si le Service Worker a t dsinstall
                if (!error.message?.includes('uninstalled')) {
                  console.log('[APP_UPDATE] Service Worker mise  jour:', error.message);
                }
              });
            }
          })
          .catch(() => {
            // Registration n'existe plus, c'est OK
          });
      }, CHECK_INTERVAL);

      return () => {
        clearInterval(interval);
        clearInterval(swInterval);
        window.removeEventListener('focus', handleFocus);
      };
    }

    return () => {
      clearInterval(interval);
      window.removeEventListener('focus', handleFocus);
    };
  }, [checkVersion, serviceWorkerRegistration]);

  // Appliquer la mise  jour
  const applyUpdate = useCallback(() => {
    // Vrifier que le Service Worker est toujours actif
    if (serviceWorkerRegistration) {
      navigator.serviceWorker.getRegistration(serviceWorkerRegistration.scope)
        .then((registration) => {
          if (registration?.waiting) {
            // Nouveau service worker en attente
            registration.waiting.postMessage({ type: 'SKIP_WAITING' });
            registration.waiting.addEventListener('statechange', (e) => {
              if (e.target.state === 'activated') {
                window.location.reload();
              }
            });
          } else {
            // Recharger la page pour appliquer les changements
            window.location.reload();
          }
        })
        .catch(() => {
          // Service Worker non disponible, recharger simplement
          window.location.reload();
        });
    } else {
      // Pas de Service Worker, recharger simplement
      window.location.reload();
    }
  }, [serviceWorkerRegistration]);

  // Ignorer la mise  jour (pour cette session)
  const ignoreUpdate = useCallback(() => {
    setUpdateAvailable(false);
    // Ne pas afficher  nouveau pendant cette session
    sessionStorage.setItem('update-ignored', 'true');
  }, []);

  return {
    updateAvailable: updateAvailable && !sessionStorage.getItem('update-ignored'),
    isChecking,
    currentVersion,
    newVersion,
    checkVersion,
    applyUpdate,
    ignoreUpdate
  };
};

