// Script pour nettoyer compltement tous les caches et forcer un refresh
export const clearAllCache = () => {
  console.log('[CLEAR_ALL_CACHE]  Dbut du nettoyage complet...');
  
  try {
    // 1. Nettoyer localStorage (sauf les lments essentiels)
    const essentialKeys = ['push_subscription']; // Garder l'abonnement push
    const currentLocalStorage = { ...localStorage };
    
    for (const key in currentLocalStorage) {
      if (!essentialKeys.includes(key)) {
        localStorage.removeItem(key);
        console.log(`[CLEAR_ALL_CACHE]  Supprim localStorage: ${key}`);
      }
    }
    
    // 2. Nettoyer sessionStorage
    sessionStorage.clear();
    console.log('[CLEAR_ALL_CACHE]  sessionStorage nettoy');
    
    // 3. Nettoyer les caches du navigateur
    if ('caches' in window) {
      caches.keys().then(cacheNames => {
        cacheNames.forEach(cacheName => {
          caches.delete(cacheName);
          console.log(`[CLEAR_ALL_CACHE]  Cache supprim: ${cacheName}`);
        });
      });
    }
    
    // 4. Forcer un rechargement complet
    console.log('[CLEAR_ALL_CACHE]  Rechargement de la page...');
    window.location.reload(true);
    
  } catch (error) {
    console.error('[CLEAR_ALL_CACHE]  Erreur lors du nettoyage:', error);
    // Forcer le rechargement mme en cas d'erreur
    window.location.reload(true);
  }
};

// Fonction pour vrifier l'tat du cache
export const checkCacheState = () => {
  const state = {
    localStorage: {},
    sessionStorage: {},
    cacheKeys: []
  };
  
  try {
    // Vrifier localStorage
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key) {
        state.localStorage[key] = localStorage.getItem(key);
      }
    }
    
    // Vrifier sessionStorage
    for (let i = 0; i < sessionStorage.length; i++) {
      const key = sessionStorage.key(i);
      if (key) {
        state.sessionStorage[key] = sessionStorage.getItem(key);
      }
    }
    
    // Vrifier les caches
    if ('caches' in window) {
      caches.keys().then(cacheNames => {
        state.cacheKeys = cacheNames;
        console.log('[CHECK_CACHE_STATE]  tat du cache:', state);
      });
    }
    
    console.log('[CHECK_CACHE_STATE]  tat du cache:', state);
    return state;
    
  } catch (error) {
    console.error('[CHECK_CACHE_STATE]  Erreur vrification cache:', error);
    return { error: error.message };
  }
};
