import { useEffect, useState } from 'react';
import { useAuth } from '../context/AuthContext';
import { BACKEND_URL } from '../config/backend';
import { registerServiceWorker } from '../utils/serviceWorkerConfig';

// Utilitaire pour convertir la cl VAPID
function urlBase64ToUint8Array(base64String) {
  const padding = "=".repeat((4 - (base64String.length % 4)) % 4);
  const base64 = (base64String + padding).replace(/-/g, "+").replace(/_/g, "/");
  const rawData = window.atob(base64);
  return Uint8Array.from([...rawData].map((c) => c.charCodeAt(0)));
}

export const usePushNotifications = () => {
  const { user } = useAuth();
  const [isSupported, setIsSupported] = useState(false);
  const [isSubscribed, setIsSubscribed] = useState(false);
  const [subscription, setSubscription] = useState(null);
  const [vapidPublicKey, setVapidPublicKey] = useState(null);
  const [hasAskedPermission, setHasAskedPermission] = useState(false);

  // Rcuprer la cl publique VAPID aprs le premier affichage (ne pas bloquer le chargement)
  useEffect(() => {
    const id = setTimeout(() => {
      const fetchVapidKey = async () => {
        try {
          const response = await fetch(`${BACKEND_URL}/api/push/vapid-public-key`);
          const data = await response.json();
          setVapidPublicKey(data.publicKey);
        } catch (error) {
          if (process.env.NODE_ENV === 'development') {
            console.error('Erreur lors de la rcupration de la cl VAPID:', error);
          }
        }
      };
      fetchVapidKey();
    }, 800);
    return () => clearTimeout(id);
  }, []);

  // Vrifier le support des notifications push et demander l'autorisation automatiquement
  useEffect(() => {
    const checkSupport = () => {
      const supported = "serviceWorker" in navigator && "PushManager" in window;
      setIsSupported(supported);
    };

    checkSupport();
  }, []);

  // Vrifier l'tat d'abonnement aprs que VAPID soit dispo (ne pas bloquer l'affichage)
  useEffect(() => {
    const checkAndRenewSubscription = async () => {
      if (!isSupported || !vapidPublicKey) {
        setHasAskedPermission(true);
        return;
      }

      const hasDeclinedPrompt = localStorage.getItem('notification-prompt-declined');
      if (hasDeclinedPrompt === 'true') {
        setHasAskedPermission(true);
        return;
      }

      try {
        const reg = await navigator.serviceWorker.ready;
        let existingSubscription = await reg.pushManager.getSubscription();
        
        if (existingSubscription) {
          if (process.env.NODE_ENV === 'production') {
            setSubscription(existingSubscription);
            setIsSubscribed(true);
            setHasAskedPermission(true);
          } else {
            const isValid = await validateSubscription(existingSubscription);
            if (isValid) {
              setSubscription(existingSubscription);
              setIsSubscribed(true);
              setHasAskedPermission(true);
            } else {
              const renewed = await renewSubscription(reg);
              if (renewed) {
                setSubscription(renewed);
                setIsSubscribed(true);
                setHasAskedPermission(true);
              } else {
                setHasAskedPermission(true);
              }
            }
          }
        } else {
          setHasAskedPermission(true);
        }
      } catch (error) {
        setHasAskedPermission(true);
      }
    };

    checkAndRenewSubscription();
  // eslint-disable-next-line react-hooks/exhaustive-deps -- excution volontaire uniquement  l'init / changement user ou vapid
  }, [isSupported, vapidPublicKey, user]);

  // Vrification intelligente et renouvellement automatique (diffre, sans bloquer l'affichage)
  useEffect(() => {
    if (!user?.id || !vapidPublicKey) return;
    let intervalId;
    const id = setTimeout(() => {
    const checkAndRenewSubscription = async () => {
      try {
        const reg = await navigator.serviceWorker.ready;
        const currentSubscription = await reg.pushManager.getSubscription();
        
        if (!currentSubscription) {
          setIsSubscribed(false);
          setSubscription(null);
          localStorage.setItem('subscription-inactive', 'true');
          localStorage.removeItem('notification-prompt-seen');
          return;
        }

        const isValid = await validateSubscriptionToken(currentSubscription);
        
        if (!isValid) {
          const renewed = await renewSubscriptionSilently(reg);
          if (renewed) {
            setIsSubscribed(true);
            setSubscription(renewed);
            localStorage.setItem('subscription-active', 'true');
            localStorage.removeItem('subscription-inactive');
          } else {
            setIsSubscribed(false);
            setSubscription(null);
            localStorage.setItem('subscription-inactive', 'true');
            localStorage.removeItem('notification-prompt-seen');
          }
        } else {
          setIsSubscribed(true);
          setSubscription(currentSubscription);
          localStorage.setItem('subscription-active', 'true');
          localStorage.removeItem('subscription-inactive');
        }
      } catch (error) {
        setIsSubscribed(false);
        setSubscription(null);
        localStorage.setItem('subscription-inactive', 'true');
      }
    };

    checkAndRenewSubscription();
    intervalId = setInterval(checkAndRenewSubscription, 6 * 60 * 60 * 1000);
    }, 1500);
    return () => {
      clearTimeout(id);
      if (intervalId) clearInterval(intervalId);
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps -- interval volontaire, pas de renouvellement  chaque changement de callback
  }, [user?.id, vapidPublicKey]);

  // Valider un token d'abonnement de manire intelligente
  const validateSubscriptionToken = async (subscription) => {
    try {
      console.log('[PUSH HOOK] Validation du token d\'abonnement...');
      
      // Vrifier si l'abonnement a les proprits requises
      if (!subscription || !subscription.endpoint || !subscription.keys) {
        console.log('[PUSH HOOK]  Abonnement invalide - proprits manquantes');
        return false;
      }

      // Vrifier si l'endpoint est valide
      if (!subscription.endpoint.startsWith('https://')) {
        console.log('[PUSH HOOK]  Endpoint invalide');
        return false;
      }

      // Vrifier si les cls sont prsentes
      if (!subscription.keys.p256dh || !subscription.keys.auth) {
        console.log('[PUSH HOOK]  Cls d\'abonnement manquantes');
        return false;
      }

      // En production, considrer comme valide si les proprits sont correctes
      if (process.env.NODE_ENV === 'production') {
        console.log('[PUSH HOOK]  Token valide (mode production)');
        return true;
      }

      // En dveloppement, tester avec le backend
      try {
        const response = await fetch(`${BACKEND_URL}/api/validate-subscription`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            subscription: subscription,
            userId: user?.id
          })
        });

        if (response.ok) {
          const result = await response.json();
          console.log('[PUSH HOOK]  Token valid par le backend');
          return result.valid;
        } else {
          console.log('[PUSH HOOK]  Erreur validation backend, considrer comme valide');
          return true; // Fallback : considrer comme valide
        }
      } catch (error) {
        console.log('[PUSH HOOK]  Erreur test backend, considrer comme valide');
        return true; // Fallback : considrer comme valide
      }
    } catch (error) {
      console.error('[PUSH HOOK] Erreur validation token:', error);
      return false;
    }
  };

  // Renouveler un abonnement de manire silencieuse
  const renewSubscriptionSilently = async (reg) => {
    try {
      console.log('[PUSH HOOK] Renouvellement silencieux de l\'abonnement...');
      
      // Dsabonner l'ancien abonnement
      const existingSubscription = await reg.pushManager.getSubscription();
      if (existingSubscription) {
        await existingSubscription.unsubscribe();
        console.log('[PUSH HOOK] Ancien abonnement dsabonn');
      }

      // Crer un nouvel abonnement
      const newSubscription = await reg.pushManager.subscribe({
        userVisibleOnly: true,
        applicationServerKey: urlBase64ToUint8Array(vapidPublicKey),
      });

      console.log('[PUSH HOOK] Nouvel abonnement cr');

      // Sauvegarder le nouvel abonnement
      const saveResponse = await fetch(`${BACKEND_URL}/api/save-subscription`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          subscription: newSubscription,
          userId: user?.id
        })
      });

      if (saveResponse.ok) {
        console.log('[PUSH HOOK]  Nouvel abonnement sauvegard');
        return newSubscription;
      } else {
        console.error('[PUSH HOOK]  Erreur sauvegarde nouvel abonnement');
        return null;
      }
    } catch (error) {
      console.error('[PUSH HOOK] Erreur renouvellement silencieux:', error);
      return null;
    }
  };

  // Valider un abonnement en testant l'envoi d'une notification (ancienne mthode)
  const validateSubscription = async (subscription) => {
    try {
      console.log('[PUSH HOOK] Validation de l\'abonnement...');
      
      // En production, ne pas envoyer de notifications de test
      if (process.env.NODE_ENV === 'production') {
        console.log('[PUSH HOOK] Mode production - validation silencieuse');
        return true; // Considrer comme valide en production
      }
      
      // En dveloppement seulement, tester l'abonnement en envoyant une notification de test
      const response = await fetch(`${BACKEND_URL}/api/test-subscription`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          subscription: subscription,
          userId: user?.id
        })
      });

      const result = await response.json();
      return result.success;
    } catch (error) {
      console.error('[PUSH HOOK] Erreur validation abonnement:', error);
      return false;
    }
  };

  // Renouveler un abonnement expir
  const renewSubscription = async (reg) => {
    try {
      console.log('[PUSH HOOK] Renouvellement de l\'abonnement...');
      
      // Dsabonner l'ancien abonnement
      const existingSubscription = await reg.pushManager.getSubscription();
      if (existingSubscription) {
        await existingSubscription.unsubscribe();
        console.log('[PUSH HOOK] Ancien abonnement dsabonn');
      }

      // Crer un nouvel abonnement
      const newSubscription = await reg.pushManager.subscribe({
        userVisibleOnly: true,
        applicationServerKey: urlBase64ToUint8Array(vapidPublicKey),
      });

      console.log('[PUSH HOOK] Nouvel abonnement cr');

      // Sauvegarder le nouvel abonnement
      const saveResponse = await fetch(`${BACKEND_URL}/api/save-subscription`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          subscription: newSubscription,
          userId: user?.id
        })
      });

      if (saveResponse.ok) {
        console.log('[PUSH HOOK]  Nouvel abonnement sauvegard');
        return newSubscription;
      } else {
        console.error('[PUSH HOOK]  Erreur sauvegarde nouvel abonnement');
        return null;
      }
    } catch (error) {
      console.error('[PUSH HOOK] Erreur renouvellement abonnement:', error);
      return null;
    }
  };

  // S'abonner aux notifications push
  const subscribeUser = async () => {
    console.log('[PUSH HOOK] Dbut de subscribeUser...', {
      isSupported,
      vapidPublicKey: !!vapidPublicKey,
      user: !!user,
      userId: user?.id
    });

    if (!isSupported || !vapidPublicKey) {
      console.log('[PUSH HOOK] Notifications push non supportes ou cl VAPID manquante');
      return false;
    }

    // En dveloppement, permettre les notifications push pour les tests
    if (process.env.NODE_ENV === 'development') {
      console.log('[PUSH HOOK] Mode dveloppement - notifications push actives pour les tests');
    }

    try {
      // Enregistrer le service worker avec la configuration
      console.log('[PUSH HOOK] Enregistrement du service worker...');
      const registration = await registerServiceWorker();

      if (!registration) {
        console.log('[PUSH HOOK] Service worker non disponible, continuation sans...');
      }

      // Demander la permission
      console.log('[PUSH HOOK] Demande de permission...');
      const permission = await Notification.requestPermission();
      console.log('[PUSH HOOK] Permission:', permission);
      
      if (permission !== "granted") {
        console.log('[PUSH HOOK] Permission refuse');
        return false;
      }

      // Vrifier si dj abonn
      console.log('[PUSH HOOK] Vrification de l\'abonnement existant...');
      const existingSubscription = registration ? await registration.pushManager.getSubscription() : null;
      if (existingSubscription) {
        setSubscription(existingSubscription);
        setIsSubscribed(true);
        console.log('[PUSH HOOK]  Dj abonn aux notifications');
        return true;
      }

      // Crer un nouvel abonnement
      console.log('[PUSH HOOK] Cration d\'un nouvel abonnement...');
      if (!registration) return false;
      const newSubscription = await registration.pushManager.subscribe({
        userVisibleOnly: true,
        applicationServerKey: urlBase64ToUint8Array(vapidPublicKey),
      });

      console.log('[PUSH HOOK]  Nouvelle subscription cre:', newSubscription);

      // Envoyer au backend avec l'userId
      console.log('[PUSH HOOK] Envoi au backend...', {
        url: `${BACKEND_URL}/api/save-subscription`,
        userId: user?.id
      });
      
      const response = await fetch(`${BACKEND_URL}/api/save-subscription`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ 
          subscription: newSubscription,
          userId: user?.id 
        }),
      });

      console.log('[PUSH HOOK] Rponse du backend:', response.status, response.ok);

      if (response.ok) {
        setSubscription(newSubscription);
        setIsSubscribed(true);
        console.log('[PUSH HOOK]  Abonnement enregistr avec succs !');
        return true;
      } else {
        const errorText = await response.text();
        console.error('[PUSH HOOK] Erreur lors de l\'enregistrement de l\'abonnement:', errorText);
        return false;
      }
    } catch (err) {
      console.error('[PUSH HOOK] Erreur abonnement:', err);
      return false;
    }
  };

  // Se dsabonner des notifications push
  const unsubscribeUser = async () => {
    if (!subscription) return false;

    try {
      await navigator.serviceWorker.ready;
      const result = await subscription.unsubscribe();
      
      if (result) {
        setSubscription(null);
        setIsSubscribed(false);
        console.log("Dsabonnement russi");
        return true;
      }
    } catch (error) {
      console.error("Erreur lors du dsabonnement:", error);
    }
    
    return false;
  };

  return {
    isSupported,
    isSubscribed,
    subscription,
    vapidPublicKey,
    hasAskedPermission,
    subscribeUser,
    unsubscribeUser
  };
};
